body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.gameArea {
    margin-left: auto;
    margin-right: auto;
    max-width: 45rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.navbar .correct {
    -webkit-animation: 1s correct;
            animation: 1s correct;
}

.navbar .incorrect {
    -webkit-animation: 1s incorrect;
            animation: 1s incorrect;
}

@-webkit-keyframes correct {
    0% {
        color:white;
    }
    20% {
        color: #2e7d32;
    }
    40% {
        color: white;
    }
    60% {
        color: #2e7d32;
    }
    80% {
        color: white;
    }
    100% {
        color: #2e7d32;
    }
}

@keyframes correct {
    0% {
        color:white;
    }
    20% {
        color: #2e7d32;
    }
    40% {
        color: white;
    }
    60% {
        color: #2e7d32;
    }
    80% {
        color: white;
    }
    100% {
        color: #2e7d32;
    }
}

@-webkit-keyframes incorrect {
    0% {
        color:white;
    }
    20% {
        color: #c62828;
    }
    40% {
        color: white;
    }
    60% {
        color: #c62828;
    }
    80% {
        color: white;
    }
    100% {
        color: #c62828;
    }
}

@keyframes incorrect {
    0% {
        color:white;
    }
    20% {
        color: #c62828;
    }
    40% {
        color: white;
    }
    60% {
        color: #c62828;
    }
    80% {
        color: white;
    }
    100% {
        color: #c62828;
    }
}
